// Font Related Js
document.getElementById("font-listing").addEventListener("click", (e) => {
  let target = e.target;
  if (target.tagName != "LI") return;
  const listAllLi = document.querySelectorAll(".fontfamily-list-item");
  [].forEach.call(listAllLi, function (el) {
    el.classList.remove("selected");
  });
  target.classList.add("selected");
});

// Typography checkbox events
const getTypographyCheckbox = document.getElementsByName(
  "select-typography-checkbox"
);

getTypographyCheckbox.forEach(function (typographyCheckBox, i) {
  typographyCheckBox.addEventListener("change", (e) => {
    if ($("#" + e.target.id).is(":checked")) {
      $("#" + e.target.id).prop("checked", true);
    } else {
      $("#" + e.target.id).prop("checked", false);
    }
  });
});

document
  .getElementById("select-all-typography")
  .addEventListener("click", (e) => {
    if ($("#select-all-typography").is(":checked")) {
      $('[name="select-typography-checkbox"]').prop("checked", true);
    } else {
      $('[name="select-typography-checkbox"]').prop("checked", false);
    }
  });

document
  .getElementById("updateFonts")
  .addEventListener("click", () => fetchSelectedFonts());

// call the webview from the plugin
window.setInitialFonts = (fontsName) => {
  let cont = document.getElementById("font-listing");
  let li = document.createElement("li"); // create li element.
  li.className = "fontfamily-list-item";
  li.innerHTML = fontsName; // assigning text to li using array value.
  cont.appendChild(li); // append li to ul.
};

const fetchSelectedFonts = function () {
  const selectedFontType = $("li.selected").text();
  const arsenicFontLayers = $('[name="select-typography-checkbox"]');

  if (selectedFontType && arsenicFontLayers) {
    for (let i = 0; i < arsenicFontLayers.length; i++) {
      const targetInput = arsenicFontLayers[i];
      const fontName = targetInput.id;
      if ($("#" + fontName).is(":checked")) {
        window.postMessage("doUpdateFonts", fontName, selectedFontType);
      }
    }

    window.postMessage("setMessage", "fonts");
  }
};
